.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

main img {
  border: 3px solid black;
}

#title {
  color: #007fff;
}

#portfolio-container {
  margin: auto;
}

#portfolio {
  margin-bottom: 250px;
}

#row-container {
  margin: auto;
}

.card {
  margin: auto;
}

.card-title {
  text-align: center;
}

.project-click-description {
  text-align: center;
  margin: 0;
  font-size: 14px;
}

form {
  width: 100%
}

#personal-info {
  margin-top: 30px;
}

footer {
  padding: 20px !important;
  width: 100%;
}

footer .container {
  margin: auto;
}

#footer-row {
  margin: auto;
  font-size: 28px;
  color: #007fff !important
}

#footer-title,
#footer-icons {
  margin: auto;
  text-align: center;
}